@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
h1.blogheading {
  font-size: 3rem;
  text-align: center;
  margin-top: 1rem;
}
main.contianerPost {
  font-family: "Poppins", sans-serif;
  width: 80%;
  height: auto;
  display: flex;
  background-color: rgb(244 240 198);
  border-radius: 1rem;
  z-index: 4;
  position: relative;
  margin: auto;
  margin: 2rem auto;
}
div.firstPost {
  width: 30%;
  border-radius: 1rem 0 0 1rem;
  overflow: hidden;
}
div.firstPost img {
  width: 100%;
  height: 100%;
}
div.second {
  width: 70%;
  padding: 1rem;
}
h1 {
  font-size: 1.5rem;
}
p {
  font-size: 1rem;
}
div.items {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
div.picture {
  display: flex;
  justify-content: center;
  align-items: center;
}
div.picture i {
  width: 3.5rem;
  height: 3.5rem;
  border-radius: 50%;
  font-size: 4rem;
}
div.items * {
  margin-right: 0.5rem;
}


@media only screen and (max-width: 500px){
  div.items {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    gap: 1rem;
    padding-left: 2rem;
    margin-top: 0;
}
}

@media only screen and (max-width: 1200px) {
  body {
    height: auto;
  }
  main.contianerPost {
    flex-direction: column;
    width: 90%;
    height: auto;
  }
  div.second {
    width: 100%;
  }
  div.firstPost {
    width: 100%;
    height: 100%;
    border-radius: 1rem 1rem 0 0;
  }
  div.items {
    margin-top: 2rem;
  }
}
@media only screen and (max-width: 328px) {
  div.items {
    margin-top: -1rem;
    position: relative;
    top: 15px;
  }
  
}